import React, { useState, useEffect } from "react";
import styled from 'styled-components';
import mailicon from "../images/faq/mail_icon.svg"
import instagramicon from "../images/faq/instagram_icon.svg"
import arrowicon from "../images/faq/bottom_arrow_icon.svg";
import { useMediaQuery } from 'react-responsive';

const applyData = [
    {
      question: '  ,        ?',
      answer: ' ,    .'
    },
    {
        question: ' ? /     ?',
        answer: ',    .'
    },
    {
        question: '      ?',
        answer: ',        . ,               .'
    },
    {
        question: '   ?',
        answer: '         ,        .'
    },
];

const planningData = [
    {
        question: '     ?',
        answer: '       . ,          .'
    },
    {
        question: '      ?',
        answer: '. ,            .'
    },
    {
      question: '      ?',
      answer: '  10        .'
    },
];

const preparationData = [
    {
      question: '  ?',
      answer: '     ,         . ,         , USB   .'
    },
    {
        question: ' API  ?',
        answer: ' API  .'
    },
    {
        question: '     ?',
        answer: '  .          ,        .'
    },
    {
        question: '       ?',
        answer: '                 .'
    },
];

const progressData = [
    {
        question: '   ?',
        answer: '   .'
    },
    {
        question: '   ,    ?',
        answer: ',      . ,         .'
    },
    {
        question: ' ?',
        answer: ',        .'
    },
    {
        question: '  ?',
        answer: '  .'
    },
    {
        question: '   ?',
        answer: '     ,    .'
    },
    {
        question: '   ?',
        answer: '        ,     . '
    },
];

const etcData = [
    {
        question: '   ?',
        answer: '          .'
    },
    {
        question: ' ? /     ?',
        answer: '    9 20      .    (24. 10. 31) .'
    },
];

const Container = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    padding : ${({ isMobile }) => (isMobile ? '0vw 5vw' : '0vw 15vw')};
`;

const FaqContainer = styled.div`
    margin-top: 4rem;
    margin-bottom: 2rem;
    position: relative;
    display: flex; 
    flex-direction: column;
    justify-content: center; /* Vertically centered */
    align-items: center;  /* Center aligned horizontally */
    text-align: center;
    
`;

const FAQ = styled.div`
    width: 100%;
    margin: 3vw auto 0 auto;
`;

const TitleContainer = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: baseline;
    background-color: transparent;
    width: clamp(70vw, 1.5vw, 60vw);
`;

const TitleText = styled.div`
    font-size: clamp(1.5rem, 1vw, 1.8rem);
    font-weight: 600;
    background-color: transparent;
`;

const GrayTitleText = styled.div`
    font-weight: 600;
    font-size: clamp(0.6rem, 1vw, 0.8rem);
    color: #949494;
    background-color: transparent;
    margin-left: 5px;
`;

const FAQAnswer = styled.div`
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease-in-out, opacity 0.3s ease-in-out;
    opacity: 0;
    padding-top: 0;
    padding-bottom: 0;
`;

const QuestionText = styled.div`
    width: 95%;
    font-size: clamp(0.9rem, 1.5vw, 1.05rem);
    background-color: transparent;
    transition: all 0.3s ease-in-out;
`

const AnswerText = styled.div`
    font-size: clamp(0.8rem, 1.5vw, 1rem);
    background-color: transparent;
    text-indent: -1rem;
    margin-left: 1rem;
`

// Unified container for both question and answer
const FaqItem = styled.div`
    background-color: rgba(0, 87, 255, 0.1);
    color: #0057FF;
    padding: 20px;
    cursor: pointer;
    border-radius: 15px;
    margin-top: 10px;
    border: 1px solid #0057FF;
    transition: all 0.3s ease-in-out;

    &.active {
        background: linear-gradient(135deg, #0057FF 0%, #003499 100%);
        color: white;

        &:hover ${QuestionText}{
            color: #FFFFFF;
            transition: 0.001s;
        }
    }
    
    &.active ${FAQAnswer} {
        background-color: transparent;
        max-height: 500px;
        padding-top: 20px;
        opacity: 1;
    }

    &:hover{
        background-color: #FFFFFF;
    }

    &:hover ${QuestionText}{
        color: #000000;
    }
`;

const FaqQuestion = styled.div`
    width: 100%;
    padding: 1px;
    background-color: transparent;
    display: flex;
    align-items: center;
    justify-content: space-between;
`;

const InquriryContainer = styled.div`
    display: flex;
    width: 100%;
    align-items: center;
    height: 30vh;
`

const Part = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
`

const BlueText = styled.div`
    color: #0057FF;
    font-size: clamp(0.8rem, 1.5vw, 1rem);
    font-weight: 400;
    margin-bottom: 5px;
`

const WhiteText = styled.div`
    font-size: clamp(0.5rem, 1.5vw, 0.8rem);
    font-weight: 400;
    background-color: transparent;
`

const InquiryInfoConatiner = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 0 auto;
`

const MailIcon = styled.div`
    background-image: url(${mailicon});
    background-repeat: no-repeat;
    background-size: contain;
    margin-right: 3px;
    width: clamp(13px, 2vw, 18px);
    height: clamp(13px, 2vw, 18px);
`

const InstagramIcon = styled.div`
    background-image: url(${instagramicon});
    background-repeat: no-repeat;
    background-size: contain;
    margin-right: 3px;
    width: clamp(13px, 2vw, 18px);
    height: clamp(13px, 2vw, 18px);
`

const ArrowIcon = styled.div`
    background-image: url(${arrowicon});
    background-repeat: no-repeat;
    background-size: contain;
    width: 15px;
    height: 15px;
    background-color: transparent;
`

const ButtonContainer = styled.div`
    display: flex;
    justify-content: center;
    align-items: center; 
    text-align: left;
    gap: 0.6rem;
    margin-bottom: 0.6rem;
    width: 100%;
`;

const Button = styled.button`
    padding: clamp(0.5rem, 0.6vw, 0.6rem) clamp(1.4rem, 1.8vw, 1.8rem);
    font-size: 0.8rem;
    font-weight: 500;
    border-color: #0057FF;
    border-radius: 30px;
    cursor: pointer;
    background-color: ${({ isActive }) => (isActive ? '#0057FF' : 'black')};
    color: ${({ isActive }) => (isActive ? 'white' : 'grey')};
    white-space: nowrap;
    

    &:hover{
        background-color: ${({ isActive }) => (isActive ? 'white' : 'transparent')} ;
        color:${({ isActive }) => (isActive ? 'black' : 'white')} ;
    }
`;


const FaqPage = () => {

    const isMobile = useMediaQuery({ query: "(max-width: 768px)" });
    const [activeIndex, setActiveIndex] = useState(null);
    const [selectPart, setSelectPart] = useState('Apply');

    useEffect(() => {
        setActiveIndex(null);
    }, [selectPart]);

    const handleToggle = (index) => {
        setActiveIndex(index === activeIndex ? null : index);
    };

    function changePart(newPart){
        if (newPart !== selectPart) {
            setSelectPart(newPart);
        }
    }

    return (
        <Container isMobile={isMobile}>
            <FaqContainer>
                <TitleContainer>
                    <TitleText>  </TitleText>
                </TitleContainer>
                <TitleContainer>
                    <GrayTitleText>FAQ</GrayTitleText>
                </TitleContainer>
                
            </FaqContainer>

            <ButtonContainer>
                <Button isActive={selectPart === 'Apply'} onClick={() => changePart('Apply')}>
                    
                </Button>
                <Button isActive={selectPart === 'Planning'} onClick={() => changePart('Planning')}>
                    
                </Button>
                <Button isActive={selectPart === 'Preparation'} onClick={() => changePart('Preparation')}>
                     
                </Button>
                
                {!isMobile && (
                    <>
                        <Button isActive={selectPart === 'Progress'} onClick={() => changePart('Progress')}>
                             
                        </Button>
                        <Button isActive={selectPart === 'Etc'} onClick={() => changePart('Etc')}>
                            
                        </Button>
                    </>
                )}
            </ButtonContainer>

            {isMobile && (
                <ButtonContainer>
                    <Button isActive={selectPart === 'Progress'} onClick={() => changePart('Progress')}>
                         
                    </Button>
                    <Button isActive={selectPart === 'Etc'} onClick={() => changePart('Etc')}>
                        
                    </Button>
                </ButtonContainer>
            )}

            {selectPart === 'Apply' && (
                <>
                    <FAQ> 
                    {applyData.map((item, index) => (
                    <FaqItem
                        key={index}
                        className={activeIndex === index ? 'active' : ''}
                        onClick={() => handleToggle(index)} // Click handler on the container
                    >
                    <FaqQuestion>
                        <QuestionText>
                            Q. {item.question}
                        </QuestionText>
                        <ArrowIcon />
                    </FaqQuestion>
                    
                    <FAQAnswer className={activeIndex === index ? 'active' : ''}>
                        <AnswerText>
                            A. {item.answer}
                        </AnswerText>
                    </FAQAnswer>
                    </FaqItem>
                    ))}
                    </FAQ>
                    
                </>
            )}

            {selectPart === 'Planning' && (
                <>
                    <FAQ> 
                    {planningData.map((item, index) => (
                    <FaqItem
                        key={index}
                        className={activeIndex === index ? 'active' : ''}
                        onClick={() => handleToggle(index)} // Click handler on the container
                    >
                    <FaqQuestion>
                        <QuestionText>
                            Q. {item.question}
                        </QuestionText>
                        <ArrowIcon />
                    </FaqQuestion>
                    
                    <FAQAnswer className={activeIndex === index ? 'active' : ''}>
                        <AnswerText>
                            A. {item.answer}
                        </AnswerText>
                    </FAQAnswer>
                    </FaqItem>
                    ))}
                    </FAQ>
                    
                </>
            )}

            {selectPart === 'Preparation' && (
                <>
                    <FAQ> 
                    {preparationData.map((item, index) => (
                    <FaqItem
                        key={index}
                        className={activeIndex === index ? 'active' : ''}
                        onClick={() => handleToggle(index)} // Click handler on the container
                    >
                    <FaqQuestion>
                        <QuestionText>
                            Q. {item.question}
                        </QuestionText>
                        <ArrowIcon />
                    </FaqQuestion>
                    <FAQAnswer className={activeIndex === index ? 'active' : ''}>
                        <AnswerText>
                            A. {item.answer}
                        </AnswerText>
                    </FAQAnswer>
                    </FaqItem>
                    ))}
                    </FAQ>
                    
                </>
            )}

            {selectPart === 'Progress' && (
                <>
                    <FAQ> 
                    {progressData.map((item, index) => (
                    <FaqItem
                        key={index}
                        className={activeIndex === index ? 'active' : ''}
                        onClick={() => handleToggle(index)} // Click handler on the container
                    >
                    <FaqQuestion>
                        <QuestionText>
                            Q. {item.question}
                        </QuestionText>
                        <ArrowIcon />
                    </FaqQuestion>
                    <FAQAnswer className={activeIndex === index ? 'active' : ''}>
                        <AnswerText>
                            A. {item.answer}
                        </AnswerText>
                    </FAQAnswer>
                    </FaqItem>
                    ))}
                    </FAQ>
                    
                </>
            )}

            {selectPart === 'Etc' && (
                <>
                    <FAQ> 
                    {etcData.map((item, index) => (
                    <FaqItem
                        key={index}
                        className={activeIndex === index ? 'active' : ''}
                        onClick={() => handleToggle(index)} // Click handler on the container
                    >
                    <FaqQuestion>
                        <QuestionText>
                            Q. {item.question}
                        </QuestionText>
                        <ArrowIcon />
                    </FaqQuestion>
                    <FAQAnswer className={activeIndex === index ? 'active' : ''}>
                        <AnswerText>
                            A. {item.answer}
                        </AnswerText>
                    </FAQAnswer>
                    </FaqItem>
                    ))}
                    </FAQ>
                    
                </>
            )}
            
            
            <FaqContainer>
                <TitleContainer>
                    <TitleText style={{ marginTop: "7vh"}}></TitleText>
                </TitleContainer>
                <TitleContainer>
                    <GrayTitleText>INQUIRY</GrayTitleText>
                </TitleContainer>
                <InquriryContainer>
                    <Part>
                        <BlueText>DACOS</BlueText>
                        <InquiryInfoConatiner>
                            <MailIcon />
                            <WhiteText>smdacos@gmail.com</WhiteText>
                        </InquiryInfoConatiner>
                        <InquiryInfoConatiner> 
                            <InstagramIcon />
                            <WhiteText>@smdacos_oficial</WhiteText>
                        </InquiryInfoConatiner>
                    </Part>
                    <Part>
                        <BlueText>SOLUX</BlueText>
                        <InquiryInfoConatiner>
                            <MailIcon />
                            <WhiteText>sm.solux@gmail.com</WhiteText>
                        </InquiryInfoConatiner>
                        
                        <InquiryInfoConatiner>
                            <InstagramIcon />
                            <WhiteText>@only_solux</WhiteText>
                        </InquiryInfoConatiner>
                    </Part>
                </InquriryContainer>
            </FaqContainer>
        </Container>
    );
};

export default FaqPage;