import { React } from "react";
import styled from 'styled-components';
import background from "../images/timeline/process_background.png";
import Button from "../component/Button";
import timeline from "../images/timeline/timeline.svg";

const TimelineContainer = styled.div`
    background-color: transparent;
`

const InfoContainer = styled.div`
    height: 90vh;
    width: 100%;
    margin-top: 0;
    margin-bottom: 40px;
    background-image: url(${background});
    background-size: contain;
    background-repeat: no-repeat;
    background-position: bottom;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`

const TitleText = styled.div`
    font-size: calc(0.26vw + 27.08px);
    font-weight: 600;
    background-color: transparent;
`

const GrayTitleText = styled.div`
    font-weight: 600;
    font-size: calc(0.26vw + 12.08px);
    color: #949494;
    background-color: transparent;
    margin-top: 5px;
    margin-bottom: 10px;
`

const GraySubtitleText = styled.div`
    font-weight: 400;
    width: 100%;
    font-size: calc(0.26vw + 11.08px);
    color: #949494;
    background-color: transparent;
    margin-top: 20px;
    text-align: center;
    line-height: 140%;
`

const SubtitleText = styled.div`
    font-size: calc(0.26vw + 11.08px);
    font-weight: 200;
    color: #B2B2B2;
    background-color: transparent;
`

const PartContainer = styled.div`
    width: 100%;
    background-color: transparent;
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    align-items: center
`

const Part = styled.div`
    background-color: transparent;
    display: flex;
    flex-direction: column;
    align-items: center;
`

const ContentText = styled.div`
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    font-weight: 400;
    font-size: calc(0.26vw + 14.08px);
    background-color: transparent;
    margin: 5px 0;
    padding-left: 0;
    text-align: center;
    align-items: center;
`;

const WhiteText = styled.span`
    color: #FFFFFF;
    background-color: transparent;
    font-size: calc(0.26vw + 14.08px);
`;

const BlueText = styled.span`
    color: #0057FF;
    background-color: transparent;
    font-size: calc(0.26vw + 14.08px);
`;


const GraySmallSubtitleText = styled.span`
    font-size: calc(0.26vw + 9.08px);
    font-weight: 200;
    color: #B2B2B2;
    background-color: transparent;
`

const TimetlineContainer = styled.div`
    width: 90%;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
`;

const TimelineImage = styled.object`
    width: 100%;
    height: auto;
    max-height: 80vh;
    display: block;
    object-fit: contain;
    background-color: transparent;
`;

const TimelinePage = () => {
    return (
        <TimelineContainer>
            <InfoContainer>
                <TitleText></TitleText>
                <GrayTitleText>APPLY</GrayTitleText>
                <SubtitleText>24. 09. 08 (SUN) 10:00 - 24. 09. 14 (SAT) 23:59</SubtitleText>
                    <PartContainer>
                        <Part style={{ marginTop: "10px"}}>
                            <BlueText> </BlueText>   
                            <WhiteText>DACOS 2   OB</WhiteText>
                        </Part>
                        <Part style={{ marginTop: "10px"}}>
                            <BlueText></BlueText>
                            <WhiteText>SOLUX 29   OB     </WhiteText>
                        </Part>
                    </PartContainer>
                    <GraySubtitleText style={{ marginBottom: "50px"}}>
                        * 5,000      .<br />
                        *   ,       .<br />
                        *          .
                    </GraySubtitleText>

                    <Button
                        onClick={() => {
                            window.open("https://linktr.ee/DxSHackathon")
                        }}
                        style = {{ height: "40px", width: "100px"}}
                        >
                        
                    </Button>   
            </InfoContainer>

            <InfoContainer>
                <TitleText> </TitleText>
                <GrayTitleText>TEAM REVEAL</GrayTitleText>
                
                <SubtitleText>2024. 10. 31 (THU)</SubtitleText>
                
                <ContentText style={{ marginTop: "60px"}}>
                    <WhiteText style={{ marginRight: "5px"}}>    </WhiteText> <BlueText>9 20 </BlueText> <WhiteText>.</WhiteText>
                </ContentText>
                <ContentText>
                    <WhiteText style={{ marginRight: "5px"}}>   2,  2,  2   .</WhiteText> <GraySmallSubtitleText>(   )</GraySmallSubtitleText>
                </ContentText>
                <ContentText>
                    <WhiteText style={{ marginRight: "5px"}}>    </WhiteText> <BlueText>  </BlueText> <WhiteText>.</WhiteText>
                </ContentText>
                <GraySmallSubtitleText style={{ marginTop: "10px"}}>*   </GraySmallSubtitleText>
            </InfoContainer>

            <InfoContainer>
                <TitleText></TitleText>
                <GrayTitleText>PRE - PREPARATION</GrayTitleText>
                
                <SubtitleText>2024. 11. 01 (FRI) - 24. 11. 07 (THU)</SubtitleText>
                
                <ContentText style={{ marginTop: "60px"}}>
                    <WhiteText style={{ marginRight: "5px"}}>         </WhiteText> <BlueText>    </BlueText> <WhiteText>.</WhiteText>
                </ContentText>
                <ContentText>
                    <WhiteText style={{ marginRight: "5px"}}> </WhiteText><BlueText>- </BlueText> <WhiteText style={{ marginRight: "5px"}}>     .</WhiteText> <GraySmallSubtitleText>(  )</GraySmallSubtitleText>
                </ContentText>
                <ContentText>
                    <WhiteText>      ,     .</WhiteText>
                </ContentText>

            </InfoContainer>

            <InfoContainer style={{ marginBottom: "40px", height: "130vh"}}>
                <TitleText style={{ marginTop: "80px"}}>  </TitleText>
                <GrayTitleText>HACKATHON</GrayTitleText>
                
                <div style={{ display: "flex", alignItems: "center"}}><SubtitleText style={{ marginRight: "5px"}}>2024. 11. 08 (FRI) 10:00 - 24. 11. 09 (SAT) 14:00</SubtitleText>
                <GraySmallSubtitleText>* 2 </GraySmallSubtitleText></div>
                
                <ContentText style={{ padding: "0px", marginTop: "30px"}}>Time table</ContentText>
                <TimetlineContainer>
                    <TimelineImage data={timeline} type="image/svg+xml" aria-label="Timeline" />
                </TimetlineContainer>

                <GraySubtitleText style={{ marginTop: "5px"}}>*, ,      </GraySubtitleText>
            </InfoContainer>
        </TimelineContainer>
    );
};

export default TimelinePage;
