import { React, useState } from "react";
import styled from 'styled-components';
import theme from '../images/hackathon/theme_background.png';
import Box from '../component/Box';
import { useMediaQuery } from 'react-responsive';

const HackathonContainer = styled.div`

`

const ThemeContainer = styled.div`
    height: 75vh;
    width: 100%;
    margin-top: 7vh;
    background-image: ${({ isMobile }) => (isMobile ? 'none' : `url(${theme})`)};
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    display: flex;
    background-position: top;
    flex-direction: column;
    justify-content: center; /*     */
    align-items: center; /*     */
    text-align: center;
`
const ThemeText = styled.div`
    font-size: 2.5rem;
    font-weight: 600;
    background-color: transparent;
`
const ThemeTitleContainer = styled.div`
    display: flex;
    align-items: baseline; 
    justify-content: center; 
    background-color: transparent;
`

const EvaluationContainer = styled.div`
    height: auto;
    width: 100%;
    margin-top: 10vh;
    padding : ${({ isMobile }) => (isMobile ? '0vw 5vw' : '0vw 15vw')};
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center; /*     */
    align-items: center; /*     */
    text-align: left;
`

const PrizeContainer = styled.div`
    height: 75vh;
    width: 100%;
    margin-top: 10vh;
    position: relative;
    padding: ${({ isMobile }) => (isMobile ? '0vw 5vw' : '0vw 15vw')};
    display: flex;
    flex-direction: column;
    justify-content: center; /*     */
    align-items: center; /*     */
    text-align: left;
`
const TitleContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: transparent;
    width: 100%;
    margin-bottom: 2vw;
`

const TitleText = styled.div`
    font-size: clamp(1.5rem, 1vw, 1.8rem);
    font-weight: 600;
    background-color: transparent;
`

const GrayTitleText = styled.div`
    font-weight: 600;
    font-size: clamp(0.6rem, 1vw, 0.8rem);
    color: #949494;
    background-color: transparent;
`

const SubtitleText = styled.div`
    font-size: 1rem;
    font-weight: 100;
    background-color: transparent;
    width: clamp(70vw, 1.5vw, 60vw);
`

const PrizeBoxContainer = styled.div`
    display: flex;
    margin-top: 1vw;
    width: 100%;
    justify-content: space-between;
`;

const PrizeBox = styled.div`
    width: 30%;
    aspect-ratio: 1.1;
    border: 1px solid #0057FF;
    background-color: ${({ opacity }) => `rgba(0, 87, 255, ${opacity})`};
    display: flex;
    justify-content: center;
    flex-direction: column; 
    align-items: center;
    text-align: center;
    border-radius: 15px; 
`;

const PrizeTitle = styled.div`
    font-weight: 500;
    font-size: clamp(1rem, 1.9vw, 1.9rem);
    background-color: transparent;
`;

const PrizeMoney = styled.div`
    font-weight: 200;
    font-size: clamp(0.7rem, 2.5vw, 1rem); 
    margin-top: clamp(0.3rem, 0.5rem, 0.5rem);
    background-color: transparent;
`;

const ButtonContainer = styled.div`
    display: flex;
    align-items: center; /*    */
    justify-content: center; 
    gap: 1vw;
    margin-top: 1vw;
    margin-bottom: 2vw;
    width: 100%;
    overflow-x: auto;
    scrollbar-width: none;
`;

const Button = styled.button`
    padding: clamp(0.4rem, 0.6vw, 0.6rem) clamp(1rem, 1.5vw, 1.5rem);
    font-size: clamp(0.7rem, 1vw, 0.8rem);
    font-weight: 500;
    border-color: #0057FF;
    border-radius: 30px;
    cursor: pointer;
    background-color: ${({ isActive }) => (isActive ? '#0057FF' : 'black')};
    color: ${({ isActive }) => (isActive ? 'white' : 'grey')};
    white-space: nowrap;

    &:hover{
        background-color: ${({ isActive }) => (isActive ? 'white' : 'transparent')} ;
        color:${({ isActive }) => (isActive ? 'black' : 'white')} ;
    }
`;

const HackathonPage = () => {

    const isMobile = useMediaQuery({ query: "(max-width: 768px)" });
    const [selectPart, setSelectPart] = useState('DS');

    function changePart(newPart){
        if (newPart !== selectPart) {
            setSelectPart(newPart);
        }
    }


    return (
        <HackathonContainer>
            <ThemeContainer isMobile={isMobile}>
                <ThemeTitleContainer>
                    <ThemeText></ThemeText>
                    <GrayTitleText>THEME</GrayTitleText>
                </ThemeTitleContainer>
                <SubtitleText> .     .</SubtitleText>  
            </ThemeContainer>
            
            <EvaluationContainer isMobile={isMobile}>
                <TitleContainer>
                    <TitleText> </TitleText>
                    <GrayTitleText>EVALUATION CRITERIA</GrayTitleText>              
                </TitleContainer>

                <ButtonContainer isMobile={isMobile}>
                    <Button isActive={selectPart === 'DS'}  onClick={() => changePart('DS')}>
                          (35)
                    </Button>
                    <Button isActive={selectPart === 'Develop'}  onClick={() => changePart('Develop')}>
                         (60)
                    </Button>
                    <Button isActive={selectPart === 'Presentation'}  onClick={() => changePart('Presentation')}>
                         (5)
                    </Button>
                </ButtonContainer>

                {selectPart === 'DS' && (
                    <>
                    <Box
                        title=' '
                        point='(10)'
                        contents={[
                            '  ',
                            '    '
                        ]}
                    />
                    <Box
                        title=''
                        point='(20)'
                        contents={[
                            '    ',
                            '     '
                        ]}
                    />
                    <Box
                        title=''
                        point='(5)'
                        contents={[
                            '   '
                        ]}
                    />
                    
                </>
                    
                )}

                {selectPart === 'Develop' && (
                    
                    <>
                        <Box
                            title=''
                            point='(10)'
                            contents={[
                                '  '
                            ]}
                        />
                        <Box
                            title=''
                            point='(25)'
                            contents={[
                                '   ()',
                                'UIUX  '
                            ]}
                        />
                        <Box
                            title='SW '
                            point='(25)'
                            contents={[
                                'SW  (   )',
                                '   '
                            ]}
                        />                        
                    </>
                )}

                {selectPart === 'Presentation' && (
                    <>
                    <Box
                        title=''
                        point='(5)'
                        contents={[
                            '    '
                        ]}
                    />                    
                </>

                )}


            </EvaluationContainer>
            
            <PrizeContainer isMobile={isMobile}>
                <TitleContainer>
                    <TitleText></TitleText>
                    <GrayTitleText>PRIZE</GrayTitleText>
                </TitleContainer>
                <PrizeBoxContainer>
                    <PrizeBox opacity={0.2}>
                        <PrizeTitle></PrizeTitle>
                        <PrizeMoney>20</PrizeMoney>
                    </PrizeBox>
                    <PrizeBox opacity={0.5}>
                        <PrizeTitle></PrizeTitle>
                        <PrizeMoney>30</PrizeMoney>
                    </PrizeBox>
                    <PrizeBox opacity={1}>
                        <PrizeTitle></PrizeTitle>
                        <PrizeMoney>50</PrizeMoney>
                    </PrizeBox>
                </PrizeBoxContainer>
            </PrizeContainer>
        </HackathonContainer>
    );
};

export default HackathonPage;
